body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  flex-wrap: wrap;
}
section {
  display: block;
  flex: 1;
  height: 100vh;
  padding: 2% 5%;
}

textarea,
#previewer {
  border: none;
  display: block;
  background-color: whitesmoke;
  width: 100%;
  height: 75%;
  min-height: 100px;
  border-radius: 5px;
}

@media (max-width: 800px) {
  .container {
    flex-direction: column;
  }
  section {
    flex: 1;
    height: 50vh;
    width: 100%;
  }
  textarea,
  #previewer {
    width: 90%;
  }
}

.editor {
  color: bisque;
  background-color: #00539cff;
}

.previewer {
  color: #00539cff;
  background-color: bisque;
}
#previewer {
  overflow: scroll;
}
textarea:focus {
  outline: none;
}
